const gallery = document.querySelector('.gallery');

const createAGalleryItem = image => {
  return `
  <li class="gallery-item">
  <a class="gallery-link" href="${image.original}">
    <img
      class="gallery-image"
      src="${image.preview}"
      alt="${image.description}"
    />
  </a>
</li>
  `;
};

const createAGallery = images.map(item => createAGalleryItem(item)).join('');

gallery.insertAdjacentHTML('afterbegin', createAGallery);

new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
  animationSpeed: 500,
  animationSlide: false,
  alertErrorMessage: 'Image not found, next image will be loaded',
});
